@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,300,100);
.cat5{
	background-color: #1A1E1F;
	margin:0;
}


/*모바일 버전*/

@media(max-width:767px) {
	.contact-items{
		width: 100%;
		height: 300px;
	}
	.header5{
		margin: 0;
		padding: 0;
		width: 100%;
		height: 60px;
		background-color: #1A1E1F;
	}
	.parag5{
		top: 30%;
		/* left: 40%; */
		text-align: center;
		position: relative;
		width: 100%;
		height: 20px;
	}
	.parag-icon5{
		text-align: center;
		width: 150px;
		height: 10px;
	}
	.kakao-logo{
		width: 50px;
		height: 50px;
		line-height: 80px;
		position: relative;
		top: 10%;
		left: -4%;
	}
	.band-logo{
		width: 50px;
		height: 50px;
		line-height: 80px;
		position: relative;
		top: 10%;
		left: -2.8%;
	}
	.cost-contact{
		clear: both;
		position: relative;
		width: 100%;
		height: 700px;
	}
	.contact-box{
		text-align: center;
		width: 300px;
		height:170px;
		float: center;
		display: block;
	  	margin: auto;
	}
	.contact-box > h2{
		font-size: 24px;
	}
	.contact-box > h4{
		font-size: 15px;
	}
	.represent1{
		margin: auto;
		text-align: center;
		width: 320px;
		height: 70px;
		font-size: 18px;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		color: #fff;
		font-weight: bold;
		padding: 22px 30px;
		border-radius: 8px;
		background-color: #004fff;
	}
	.represent2{
		margin: auto;
		text-align: center;
		width: 320px;
		height: 70px;
		font-size: 18px;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		color: #371C1D;
		font-weight: bold;
		line-height: 70px;
		border-radius: 8px;
		z-index: 1;
		background-color: #F9E000;
	}
	.acc1{
		z-index: 5;
		font-size: 18px;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		color: #371C1D;
		font-weight: bold;
		bottom: 25%;
	}
	.acc2{
		z-index: 5;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		font-size: 18px;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		color: #fff;
		font-weight: bold;
		bottom: 25%;
	}
	.represent3{
		top: 0;
		margin: auto;
		/*div 태그 안의 텍스트를 세로로 가운데 배치하고 싶으면 line-height를 높이랑 같이 주면 되*/
		text-align: center;
		width: 320px;
		height: 70px;
		font-size: 18px;
		/*inline-block으로 설정하고 margin: auto하면 가운데 정렬 안됨. width 설정해주고 margin: auto 줘야 중앙정렬됨*/
		color: #fff;
		font-weight: bold;
		line-height: 70px;
		border-radius: 8px;
		background-color: #1BCC21;
	}

	.costTable > h2{
		font-size: 24px;
		text-align: center;
		top: 10%;
	}
	.costTable > h3{
		font-size: 15px;
		width: 80%;
		margin: auto;
		text-align: center;
		top: 13%;
	}
	.last-text{
		color: black;
		text-align: center;
		font-weight: 800;
	}
	.column-text{
		color: white;
	}
	.text-left{
		color: black;
		text-align: center;
	}
	.costTable{
		width: 100%;
		clear: both;
		height: 800px;
		position: relative;
	}
	.header5{
		margin: 0;
		padding: 0;
		width: 100%;
		height: 80px;
		background-color: #1A1E1F;
	}

	body {
	  font-family: "Roboto", helvetica, arial, sans-serif;
	  font-size: 16px;
	  font-weight: 400;
	  text-rendering: optimizeLegibility;
	}

	div.table-title {
	  top: 20%;
	  position: relative;
	  margin: auto;
	  width: 85%;
	  color: black;
	  border: #2F313D solid 2.5px; 
	}

	/*** Table Styles **/
	.table-fill {
	  
	  background: white;
	  border-radius:3px;
	  border-collapse: collapse;
	  height: 320px;
	  margin: auto;
	  max-width: 100%;
	  padding:5px;
	  width: 100%;
	  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
	  animation: float 5s infinite;
	}

	th {
	  color:#D5DDE5;
	  background:#1b1e24;
	  border-bottom:4px solid #9ea7af;
	  border-right: 1px solid #343a45;
	  font-size:14px;
	  font-weight: 100;
	  padding:20px;
	  text-align:left;
	  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
	  vertical-align:middle;
	}

	th:first-child {
	  border-top-left-radius:3px;
	}

	th:last-child {
	  border-top-right-radius:3px;
	  border-right:none;
	}

	tr {
	  border-top: 1px solid #C1C3D1;
	  border-bottom-: 1px solid #C1C3D1;
	  color:#666B85;
	  font-size:16px;
	  font-weight:normal;
	  text-shadow: 0 1px 1px rgba(256, 256, 256, 0.1);
	}

	tr:hover td {
	  background:#4E5066;
	  color:#FFFFFF;
	  border-top: 1px solid #22262e;
	}

	tr:first-child {
	  border-top:none;
	}

	tr:last-child {
	  border-bottom:none;
	}

	tr:nth-child(odd) td {
	  background:#EBEBEB;
	}

	tr:nth-child(odd):hover td {
	  background:#4E5066;
	}

	tr:last-child td:first-child {
	  border-bottom-left-radius:3px;
	}

	tr:last-child td:last-child {
	  border-bottom-right-radius:3px;
	}

	td {
	  background:#FFFFFF;
	  padding:7px;
	  text-align:left;
	  vertical-align:middle;
	  font-weight:300;
	  font-size:15px;
	  text-shadow: -1px -1px 1px rgba(0, 0, 0, 0.1);
	  border-right: 1px solid #C1C3D1;
	}

	td:last-child {
	  border-right: 0px;
	}

	th.text-left {
	  text-align: left;
	}

	th.text-center {
	  text-align: center;
	}

	th.text-right {
	  text-align: right;
	}

	td.text-left {
	  text-align: left;
	}

	td.text-center {
	  text-align: center;
	}

	td.text-right {
	  text-align: right;
	}
}

